import { graphql } from "gatsby";

import React from "react";
import MainSectionWrapper from "../layout";
import CommonHead from "../layout/Head";
import PetitsAnimaux from "../layout/pages/PetitsAnimauxHome";
import JsonLd from "../layout/jsonLd";
import { homeJsonLd } from "../seo/jsonLd";

const PageTemplate = (props) => {
  const {
    allGoogleSpreadsheetPetitsAnimauxCategories,
    allGooglePhotosPhoto,
  } = props.data;

  const categories = React.useMemo(() => {
    return allGoogleSpreadsheetPetitsAnimauxCategories.nodes.map((node) => {
      return {
        ...node,
        active: Boolean(node.active.toLowerCase() === 'true')
      };
    });
  }, [allGoogleSpreadsheetPetitsAnimauxCategories]);

  const photos = React.useMemo(() => {
    return allGooglePhotosPhoto.nodes.filter(photo => photo.description);
  }, [allGooglePhotosPhoto]);

  return (
    <>
      <MainSectionWrapper>
        <PetitsAnimaux {...props}
          categories={categories}
          photos={photos}
        />
      </MainSectionWrapper>

      <JsonLd data={homeJsonLd} />
    </>
  )
};

export function Head(props) {
  const data = {
    page: {
      name: "Crochet Nest Home",
    }
  };

  return <CommonHead {...props} data={data} />;
}

export const query = graphql`
  query PetitsAnimaux {
    allGoogleSpreadsheetPetitsAnimauxCategories {
      nodes {
        coverPhoto 
        category
        title
        active 
      }
    }

    allGooglePhotosPhoto(filter: {album: {title: {eq: "P2F"} } }) {
      nodes {
        id
        file {
          childImageSharp {
            gatsbyImageData(
              height: 160
            )
          }
        }
        description
      }
    }

  }
`;

export default PageTemplate;